:root {
  --font-color: white;
  --border-color: rgb(47, 51, 54);
  --active-color: #1A92DC;
  --body-color: none;
  --nav-color: black;
  --article-color: black;
}
*{
  box-sizing: border-box !important;
}
body {
  
  margin: 0;
  box-sizing: border-box;
  background-color: var(--body-color);
  color: var(--font-color);
  height: 100vh;
}

a{
  text-decoration: none;
 color:var(--font-color);
 display: block; 
}