.sidebar_item{
    /* background-color: black; */
    font-size:  20px;
    font-weight: 700;
    height: 75%;
    border-right:1px solid var(--border-color);
}

.sidebar_item:hover a span{
    color:var(--active-color) !important
}

.Link{
    display: block;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    /* flex-direction: column; */
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100%; 
} 
.icon{
    width:30px;
    height:30px;
}
.Link:hover .icon{
    background-color: var(--active-color) !important;
}
@media screen and (max-width:900px) {
    .sidebar_item{
        flex: 0  0 100px;
        height: 80%;
        /* font-size:  20px; */

    }

    .Link{
        padding: 0; 
        padding-left: 10px; 
        border-right:1px solid var(--border-color) ;
    }   
    .name{
        font-size: 10px;
    }
    .icon{
       height:50px;
    }
}