.container{
    /* border:11px solid blue; */
    width:65%;
    height:100%;
    margin: auto;
    /* display: flex; */

}
.motorContainer{
    border:2px solid var(--border-color);
    width:100%;
    height:500px;
   margin-top: 50px;
}
.motorContainer header{
    /* border:3px solid fuchsia; */
    width:100%;
    height:10%;
    text-align: left;
    display: flex;
    align-items: center;
    padding:0 30px;
    background-color: var(--article-color);
}
.motorContainer header span{
    font-size: 18px;
}

.motorContainer main{
    /* border:1px solid var(--border-color); */
    width:100%;
    height:60%;
    background-color: var(--article-color);
    padding: 0 30px;

}

.motorContainer main img{
    width:100%;
    height:100%;
}
.motorContainer footer{
    padding: 10px 30px;
    text-align: left;
    /* border:3px solid yellow; */
    width:100%;
    height:30%;
    background-color: var(--article-color);

}