.mainContainer{
    position: fixed;
    width: 100%;
    height: 100px;
}
.container{
    height: 100%;
    padding: 0 ;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    /* flex: 0 0 10%; */
    background-color: var(--nav-color);
}
.sidebar_list{
    opacity: 1;
    display: flex;
    align-items: center;
    overflow: auto;
    height: 100%;
    list-style: none;
    
    
} 
.logOut{
    padding: 0;
    margin-left: 100px;
    
}
.name{
    display: none;
}
.info{
    padding:10px;
    margin-bottom:0px;
}
.image {
    height: 50px;
    width: 50px;
    border-radius:50%;
}
.icon{
    display: inline-block;
    width:50px;
    height:50px;
    margin-left:10px;
    background-color:var(--active-color);

}
.logo{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
}
@media screen and (max-width:900px) {
    .container{
        padding: 0 10px;
    }    
    .logo{
        font-size: 16px;
    }
}