.body{
    display: flex;
    box-sizing: border-box;
}
.container2 {
    flex-grow:1;
    padding: 30px;
    background-color: var(--body-color);
    box-sizing: border-box;
    padding-top: 200px;
}
.border{
    position: fixed;
    top: 100px;
    width: 100%;
    display: block;
    border: 3px solid var(--border-color);
    /* margin-top: 100px; */
}
@media  screen and (max-width: 2700px) {
    .body{
       flex-direction: column;
    }
}